@import './settings.scss';

.c-current-weather {
  display: flex;
  flex-direction: row;
  margin: 1rem 1rem 0;

  &__left {
    flex: 1 0 50%
  }

  &__right {
    flex: 1 0 50%;
    margin-top: 1.5rem;
  }

  &__date {
    @include s-font-type--1;
  }

  &__title {
    @include s-heading-type--1; 
  }

  &__description {
    @include s-font-type--1;
    text-transform: capitalize;
  }

  &__icon {
    width: 3rem;
    height: 2rem;
    margin: 0.2rem -0.2rem 0 -0.7rem;

    @supports (object-fit: cover) {
      object-fit: cover;
    }
  }

  &__temp {
    display: flex;
    align-items: center;
    line-height: 1;

    .is-temp {
      font-size: 1.5rem;
      vertical-align: middle;
    }

    .is-units {
      @include s-font-type--1;
      margin: 0 0.2rem 0.35rem;
      opacity: $s-opacity--light;

      &.is-toggle {
        margin: 0 0.2rem 0.35rem;
        font-weight: 400;
        line-height: 1;
        padding: 0;
        color: $s-color--toggle;
        appearance: none;
        border: none;
        background-color: transparent;
        cursor: pointer;
      }

      &:last-child {
        padding-left: 0.2rem;
        border-left: 1px solid $s-color--shadow;
      }
    }
  }

  &__detail {
    @include s-font-type--2;

    .is-label {
      opacity: $s-opacity--light;
    }
  }

  // Desktop and up
  @media screen and (min-width: 1100px) {
    &__icon {
      width: 4rem;
      height: 3rem;
      margin: 0.3rem -0.2rem 0 -1rem;
    }

    &__temp {
      .is-temp {
        font-size: 2.5rem;
      }

      .is-units {
        margin: 0 0.3rem 0.8rem;

        &.is-toggle {
          margin: 0 0.3rem 0.8rem;
        }
      }
    }
  }
}