@import './settings.scss';

.c-location-picker {
  margin: 0 1rem;
}

.react-autosuggest {
  &__container {
    position: relative;
  }

  &__input {
    @include s-font-type--1;
    width: 16rem;
    height: 1rem;
    padding: 0.5rem 1rem;
    border: 1px solid #aaa;
    border-radius: 4px;
  }

  &__input--focused {
    outline: none;
  }

  &__input--open {
    border-bottom-left-radius: 0;
    border-bottom-right-radius: 0;
  }

  &__suggestions-container {
    display: none;
  }

  &__suggestions-container--open {
    @include s-font-type--1;
    display: block;
    position: absolute;
    top: 34px;
    width: 288px;
    border: 1px solid #aaa;
    background-color: #fff;
    border-bottom-left-radius: 4px;
    border-bottom-right-radius: 4px;
    z-index: 2;
  }

  &__suggestions-list {
    margin: 0;
    padding: 0;
    list-style-type: none;
  }

  &__suggestion {
    cursor: pointer;
    padding: 0.5rem 1rem;
  }

  &__suggestion--highlighted {
    background-color: #ddd;
  }

  @media screen and (min-width: 1100px) {
    &__input {
      padding: 1rem 1rem;
    }

    &__suggestion {
      padding: 1rem 1rem;
    }

    &__suggestions-container--open {
      top: 49px;
    }
  }
}