// Settings
$s-opacity--light: 0.7;
$s-color--shadow: #bfbfbf;
$s-color--toggle: #70757a;

// Mixins
@mixin s-heading-type--1 {
  // font-size: 0.63rem;
  font-weight: 400;
  margin: 0;

  @media screen and (min-width: 1100px) {
    font-size: 2rem;
  }
}

@mixin s-heading-type--2 {
  font-size: 0.63rem;
  font-weight: 600;
  margin: 0;

  @media screen and (min-width: 1100px) {
    font-size: 0.88rem;
  }
}

@mixin s-font-type--1 {
  font-size: 0.75rem;
  font-family: inherit;
  margin: 0;

  @media screen and (min-width: 1100px) {
    font-size: 1rem;
  }
}

@mixin s-font-type--2 {
  font-size: 0.75rem;
  margin: 0;

  @media screen and (min-width: 1100px) {
    font-size: 0.88rem;
  }
}

@mixin s-font-type--3 {
  font-size: 0.63rem;
  margin: 0;

  @media screen and (min-width: 1100px) {
    font-size: 0.75rem;
  }
}

