@import './settings.scss';

// Centre our widget
.c-weather-widget {
  max-width: 55rem;
  margin: 30vh auto 0;
  box-shadow: 5px 5px 10px -5px $s-color--shadow;

  @media screen and (min-width: 1100px) {
    padding: 1rem;
  }

  .is-error, .is-loading {
    margin: 0;
    margin: 1rem 1rem;

    &:first-of-type {
      margin-top: 2rem;
    }
  }
}