@import './settings.scss';

.c-weather-tiles {
  display: flex;
  position: relative;
  flex-direction: row;
  flex-wrap: nowrap;
  overflow-x: auto;
  scroll-snap-type: x mandatory;
  scroll-padding: 0.5rem;
  margin-top: 1rem;

  // &:after {
  //   content: '';
  //   position: fixed;
  //   right: 0;
  //   width: 1.5rem;
  //   height: 100%;
  //   background: linear-gradient(90deg, transparent 0%, #e2e2e2 100%);
  // }

  &__tile {
    display: flex;
    flex-direction: column;
    flex: 1;
    text-align: center;
    min-width: 4.5rem;
    scroll-snap-align: start;
    padding-bottom: 1rem;

    &:first-child {
      padding-left: 0.5rem;
    }

    &:last-child {
      padding-right: 0.5rem;
    }

    &-title {
      @include s-heading-type--2;
    }

    &-icon {
      width: 3rem;
      height: 2rem;
      margin: 0.3rem auto;

      @supports (object-fit: cover) {
        object-fit: cover;
      }
    }

    &-temp {
      @include s-font-type--1;

      .is-low {
        opacity: $s-opacity--light;
      }

      .is-high {
        margin-right: 0.2rem;
      }
    }

    &-rain {
      @include s-font-type--3;

      .is-label {
        opacity: $s-opacity--light;
        margin-right: 0.2rem;
      }
    }
  }

  // Desktop and up
  @media screen and (min-width: 1100px) {
    margin-top: 2rem;

    &__tile {
      &-icon {
        width: 4rem;
        height: 3rem;
      }
    }
  }
}